<template>
  <div id="useHelp">
       <ul class="navList">
	      <li :class="liActive1?'active_li':''"  @click="liClick1">取车指南  <span></span></li>
	      <li :class="liActive2?'active_li':''"  @click="liClick2">问题解答 <span></span></li>
       </ul>
       <leftDiv v-if="divShow"></leftDiv>
       <rightDiv v-else></rightDiv>

  </div>
</template>

<script>
	const leftDiv = () => import("../useHelp/left.vue");
	const rightDiv = () => import("../useHelp/right.vue");

  export default {
    name: "userName",
    components:{
    	leftDiv,
    	rightDiv
    },
    data () {
      return {
      	liActive1:true,
      	liActive2:false,
        divShow:true
      }
    },
    methods:{
    	 liClick1(){
    	 	this.liActive1=true;
    	 	this.liActive2=false;
    	 	this.divShow=true;
    	 },
    	 liClick2(){
         this.divShow=false;
    	 	this.liActive1=false;
    	 	this.liActive2=true;
    	 },
    }
  }
</script>

<style scoped  lang="scss"  >

.navList{
	height: 80px;
	width: 100%;
	display: flex;
	border-bottom: 1px solid #F0F0F0;
	li{
		 flex: 1;
		 text-align: center;
		 height: 80px;
		 line-height: 80px;
		 font-size:26px;
		font-family:PingFangSC-Regular;
		font-weight:400;
		color:rgba(72,72,72,1);
		position: relative;
		span{
			width:80px;
			height:2px;
			background:rgba(255,141,89,1);
			border-radius:2px;
			bottom:1px;
			position: absolute;
			left: 50%;
			margin-left: -40px;
			display: none;
		}
	}

	.active_li{
		color: #FF8D59;
		span{
			display: block;
		}
	}
}
.header {
  text-align: center;
  color: #fff;
  position: relative;
  height: 88px;
}
.header  span{
  font-size:32px;
  font-family:PingFangSC-Medium;
  font-weight:600;
  color:rgba(51,51,51,1);
  line-height: 88px;
}

.header i{
  content: "";
  position: absolute;
  top: 13px;
  left: 30px;
  margin: 20px;
  border-right: 4px solid rgba(51,51,51,1);
  border-bottom: 4px solid rgba(51,51,51,1);
  width: 18px;
  height: 18px;
  transform: matrix(-0.71,-0.71, -0.71, 0.71, 0, 0)
}

</style>



